<template>
    <div class="box">
        <!-- {{item}} -->
        <!-- <div class="top">
            <div @click="$router.go(-1)">
                &lt;
            </div>
            <div>
                {{ item.title }}
               
            </div>
        </div> -->
        <van-nav-bar
            :title="item.title"
            left-text="返回"
            left-arrow
            @click-left="$router.go(-1)"
        />
        <div class="main">
            <van-swipe class="swipers" :autoplay="3000" indicator-color="white">
                <van-swipe-item><img :src="item.img" alt=""></van-swipe-item>
                <van-swipe-item><img :src="item.img" alt=""></van-swipe-item>
                <van-swipe-item><img :src="item.img" alt=""></van-swipe-item>
                <van-swipe-item><img :src="item.img" alt=""></van-swipe-item>
            </van-swipe>
            
            <p> {{ item.info }} <span style="color:red;">{{ item.num }}</span></p>
        </div>
        
        <div class="bot " >
           <van-button type="primary" @click="add(item.id)" :disabled="item.num == 0" size="large">添加购物车{{ total }}</van-button>
        </div>
    </div>
</template>

<script>
import { mapState , mapMutations , mapGetters } from "vuex"
export default {
    data() {
        return {

        };
    },
    computed: {
        ...mapState({
            item:function(state){
                return state.list.arr.find(ele => ele.id == this.$route.params.id)
            }
        }),
        ...mapGetters({
            total:"list/total"
        })
    },
    methods: {
        ...mapMutations({
            add:"list/add"
        })
    },
};
</script>

<style scoped lang="scss">
.opc{
    opacity: .6;
}
.box{
    height: 100%;
    display: flex;
    flex-direction: column;
    .main{
        flex: 1;
        overflow: auto;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .top,.bot{
        height: 50px;
        display: flex;
        align-items: center;
        background: #fff;
        border-bottom: 1px solid #ccc;
    }
    .bot{
        border-top: 1px solid #ccc;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
</style>
<style lang="scss" >
.main .swipers{
    width: 100%;
    img{
        width: 100%;
    }
}
</style>